import React from "react";
import {Link} from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Container, Row, Col, Navbar, Nav, NavDropdown, Modal, Button, Form, FormControl } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { faFacebook, faInstagram, faTwitter, faPatreon, faBandcamp, faApple, faAmazon, faYoutube, faSpotify, faSoundcloud } from "@fortawesome/free-brands-svg-icons";

function SocialModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Follow me on Social Media, stream my music, and support my art!
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Container>
          <Row>
            <Col>
              <h4 ><FontAwesomeIcon icon={faBandcamp} className="fa" /> <FontAwesomeIcon icon={faSpotify} className="fa" /> <FontAwesomeIcon icon={faSoundcloud} className="fa" /><FontAwesomeIcon icon={faYoutube} className="fa" /><FontAwesomeIcon icon={faFacebook} className="fa" /> <FontAwesomeIcon icon={faInstagram} className="fa"/> <FontAwesomeIcon icon={faTwitter} className="fa" /> <FontAwesomeIcon icon={faApple} className="fa" /> <FontAwesomeIcon icon={faAmazon} className="fa" /></h4>
            </Col>
          </Row>
        </Container>  
        <h4></h4>
        <p>
        </p>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}


const linkStyle = {
    
    textDecoration: "none",
    color: 'white'
  };

  const inlinkStyle = {
    
    textDecoration: "none",
    color: 'black'
  };

function MyNavbar() {
  const [modalShow, setModalShow] = React.useState(false);
   return (
    <Container fluid> 
           
        <Navbar className="stickytop" collapseOnSelect expand="xxl" bg="dark" variant="dark">
        <Row>
      <Col className="d-flex justify-contents-center">
  <Navbar.Brand><Link to="/" style={linkStyle}><img  src="https://i.ibb.co/d2vgWr7/removal-ai-tmp-61821f028a039.png" alt="removal-ai-tmp-61821f028a039" border="0" height="70" /></Link></Navbar.Brand>
  </Col>
  
  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
  <Navbar.Collapse id="responsive-navbar-nav">
    <Col className="flex-d justify-contents-right">
    <Nav>
    <Nav.Link><Link to="/" style={linkStyle}>Featured</Link></Nav.Link>
      <Nav.Link><Link to="/calendar" style={linkStyle}>Event Calendar</Link></Nav.Link>
      <NavDropdown title="Discography" id="collasible-nav-dropdown">
        <NavDropdown.Item><Link to="/Listen/whiskey_lullabies" style={inlinkStyle}>Whiskey Lullabies</Link></NavDropdown.Item>
        <NavDropdown.Item><Link to="/Listen/por_deseo" style={inlinkStyle}>Por Deseo</Link></NavDropdown.Item>
        <NavDropdown.Item><Link to="/Listen/rain" style={inlinkStyle}>RAIN</Link></NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item><Link to="/Listen" style={inlinkStyle}>View Full Discography</Link></NavDropdown.Item>
      </NavDropdown>
      <Nav.Link><Link to="/shop" style={linkStyle}>Shop</Link></Nav.Link>
    </Nav>
    </Col>
    <Col>
    <Nav>
      <Nav.Link onClick={() => setModalShow(true)}>Socials</Nav.Link>
      <Nav.Link href="http://patreon.com">
        Patreon <FontAwesomeIcon icon={faPatreon} />
      </Nav.Link>
    </Nav>
    </Col>
  </Navbar.Collapse>
  <SocialModal
        show={modalShow}
        onHide={() => setModalShow(false)}
      />
  </Row>
</Navbar>
</Container>


    )}

export default MyNavbar